.borda {
  border-right: 1px solid black;
  border-bottom: 1px solid black;
}

.topo {
  border-top: 1px solid black;
}

.linha {
  display: flex;
  flex-direction: row;
}

.celula {
  display: flex;
  justify-content: center;
  width: 5rem;
  height: 1rem;
  padding: 0.3rem;
}

.porcentagem {
  font-weight: 200;
  font-size: 0.8rem;
}

.carga-horaria-docente {
  padding-right: 0.5rem;
}

.primeira-coluna {
  border-left: 1px solid black;
  display: flexbox;
  width: 20rem;
}

.coluna-carga-horaria {
  display: flexbox;
  width: 3rem;
}

.contaianer {
  display: flex;
  flex-direction: column;
}

.colapsavel {
  cursor: pointer;
}

.colapsavel:hover {
  filter: contrast(120%);
}

.texto-esquerda {
  text-align: left;
}
